.wrapper {
  margin: 100px 0 150px;
}
.header {
  padding: 25px 0;
  text-align: center;
  background: #222;
}

.header img {
  width: 100px;
  height: 13px;
}
.tesla-battery {
  width: 1050px;
  margin: 0 auto;
}

.tesla-battery h1 {
  font-family: 'RobotoNormal';
    font-weight: 100;
    font-size: 38px;
    text-align: center;
    letter-spacing: 3px;
}   

.tesla-climate-container {
  float: left;
  width: 420px;
  padding: 0 40px;
  margin: 0 40px 0 0;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.tesla-controls {
  display: block;
  width: 100%;
}
.tesla-battery__notice {
    margin: 20px 0;
    font-size: 15px;
    color: #666;
    line-height: 20px;
} 
.tesla-car {
  width: 100%;
  min-height: 350px;
  background: #fff url(/traderbox/static/media/tesla.472d2fe2.jpg) no-repeat top center;
  background-size: contain; }

.tesla-wheels {
  height: 247px;
  width: 555px;
  position: relative;
  margin: 0 auto; }

.tesla-wheel {
  height: 80px;
  width: 80px;
  bottom: 0;
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }
  .tesla-wheel--front {
    left: 53px; }
  .tesla-wheel--rear {
    right: 72px; }
  .tesla-wheel--19 {
    background-image: url(/traderbox/static/media/wheel-19.000182ec.png);
    animation: infinite-spinning 250ms steps(6) infinite; }
  .tesla-wheel--21 {
    background-image: url(/traderbox/static/media/wheel-21.a3203d2a.png);
    animation: infinite-spinning 480ms steps(12) infinite; }

@keyframes infinite-spinning {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.tesla-stats {
  margin: -70px 0 30px; }
  .tesla-stats ul {
    text-align: center; }
    .tesla-stats ul li {
      display: inline-block;
      width: 130px;
      position: relative; }
      .tesla-stats ul li p {
        font-size: 40px;
        font-weight: normal;
        font-family: 'RobotoNormal';
        display: block;
        padding: 0 18px 0 0;
        position: relative;
        color: #008dff;
        text-align: right; }
        .tesla-stats ul li p:after {
          font-size: 14px;
          font-weight: normal;
          font-family: 'RobotoNormal';
          content: 'MI';
          position: absolute;
          top: 8px;
          right: 0; }
  .tesla-stats-icon {
    height: 20px;
    background-size: auto 13px;
    background-position: top right;
    background-repeat: no-repeat; }
    .tesla-stats-icon--60 {
      background-image: url(/traderbox/static/media/60.9c9626af.svg); }
    .tesla-stats-icon--60d {
      background-image: url(/traderbox/static/media/60d.3658a79e.svg); }
    .tesla-stats-icon--75 {
      background-image: url(/traderbox/static/media/75.81db1754.svg); }
    .tesla-stats-icon--75d {
      background-image: url(/traderbox/static/media/75d.4f8bb511.svg); }
    .tesla-stats-icon--90d {
      background-image: url(/traderbox/static/media/90d.0d20113e.svg); }
    .tesla-stats-icon--p100d {
      background-image: url(/traderbox/static/media/p100d.405cff92.svg); }
.tesla-counter {
  float: left;
  width: 230px; }
  .tesla-counter__title {
    letter-spacing: 2px;
    font-size: 16px; }
  .tesla-counter__container {
    margin: 10px 0 0;
    padding-right: 40px; }
    .tesla-counter__container input[type=number] {
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px; }
  .tesla-counter__number {
    font-family: 'RobotoNormal';
    font-size: 25px;
    line-height: 25px;
    font-weight: 400;
    position: relative; }
    .tesla-counter__number span {
      position: absolute;
      top: 0;
      left: 35px;
      font-size: 15px;
      text-transform: uppercase; }
  .tesla-counter__item {
    position: relative;
    width: 100%;
    height: 65px;
    border: 1px solid #ccc;
    display: inline-block;
    padding: 18px 0 0 30px;
    margin: 0 8px 0 0;
    background-color: #f7f7f7;
    background-position: 24.21053% 9px;
    background-repeat: no-repeat;
    background-size: 44px; }
    .tesla-counter__item:focus {
      background-color: #f2f2f2;
      outline: none; }
  .tesla-counter__controls {
    position: absolute;
    right: 10px;
    top: 7px; }
    .tesla-counter__controls button {
      outline: 0;
      width: 30px;
      color: #008dff;
      cursor: pointer;
      display: block;
      padding: 11px 0;
      vertical-align: middle;
      border: 0;
      background-size: 60%;
      background-position: center;
      background-repeat: no-repeat;
      background-color: transparent; }
      .tesla-counter__controls button[disabled] {
        opacity: 0.4;
        cursor: not-allowed; }
      .tesla-counter__controls button:first-child {
        border-bottom: 1px solid #fff;
        background-image: url(/traderbox/static/media/up.9240e048.svg); }
      .tesla-counter__controls button:last-child {
        border-top: 1px solid #ccc;
        background-image: url(/traderbox/static/media/down.96b579fe.svg); }

.tesla-climate {
  float: left; }
  .tesla-climate__item {
    cursor: pointer;
    display: block;
    width: 100px;
    height: 100px;
    border: 6px solid #f7f7f7;
    border-radius: 50%;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
    color: #666;
    background: #fff; }
    .tesla-climate__item--active {
      color: #fff;
      background: #33a0ff;
      background: linear-gradient(to bottom, #33a0ff 0%, #388bff 100%); }
      .tesla-climate__item--active.tesla-heat {
        background: #d64800;
        background: linear-gradient(to bottom, #d64800 0%, #d20200 100%); }
  .tesla-climate__icon {
    display: block;
    width: 22px;
    height: 22px;
    margin: 8px auto 0;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(/traderbox/static/media/ac-off.6fd1e80d.svg); }
    .tesla-heat .tesla-climate__icon {
      background-image: url(/traderbox/static/media/heat-off.59ad97f6.svg); }
    .tesla-climate__item--active .tesla-climate__icon {
      background-image: url(/traderbox/static/media/ac-on.1dbeabad.svg); }
    .tesla-climate__item--active.tesla-heat .tesla-climate__icon {
      background-image: url(/traderbox/static/media/heat-on.a58771b9.svg); }
  .tesla-climate p {
    margin: 14px 0 0;
    text-align: center;
    font-size: 10px;
    text-transform: uppercase; }
  .tesla-climate input[type=checkbox] {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px; }
.tesla-wheels__component {
  float: left;
  width: 355px;
}
.tesla-wheels__title {
  letter-spacing: 2px;
  font-size: 16px;
}
.tesla-wheels__container {
  margin: 10px 0 0;
}
.tesla-wheels__item {
  cursor: pointer;
  width: 47%;
  height: 65px;
  border: 1px solid #ccc;
  display: inline-block;
  padding: 20px 0 0 90px;
  margin: 0 8px 0 0;
  background-color: #f7f7f7;
  background-position: 24.21053% 9px;
  background-repeat: no-repeat;
  background-size: 44px;
}
.tesla-wheels__item--19 {
  background-image: url(/traderbox/static/media/19.4c1cb027.svg);
}
.tesla-wheels__item--21 {
  background-image: url(/traderbox/static/media/21.118eabde.svg);
}
.tesla-wheels__item--focused {
  background-color: #f2f2f2;
}
.tesla-wheels__item--active {
  border-color: #39f;
  box-shadow: inset 0px 0px 0px 1px #39f;
}
.tesla-wheels__item p {
  font-family: 'RobotoNormal';
  font-size: 16px;
  font-weight: 400;
  color: #333;
}
.tesla-wheels__item input[type=radio] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

@font-face {
  font-family: 'RobotoNormal';
  src: url(/traderbox/static/media/Roboto-Regular-webfont.86c03c03.eot);
  src: url(/traderbox/static/media/Roboto-Regular-webfont.86c03c03.eot?#iefix) format('embedded-opentype'),
       url(/traderbox/static/media/Roboto-Regular-webfont.90564028.woff) format('woff'),
       url(/traderbox/static/media/Roboto-Regular-webfont.447f07f0.ttf) format('truetype'),
       url(/traderbox/static/media/Roboto-Regular-webfont.2e416dfb.svg#RobotoRegular) format('svg');
  font-weight: normal;
  font-style: normal;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font: 300 14px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.cf:before,
.cf:after {
    content: '';
    display: table;
}
.cf:after {
    clear: both;
}
.cf {
  *zoom: 1;
}

